<template>
  <v-content>
    <v-container>
      <v-data-table
        :headers="headers"
        :items="desserts"
        :search="search"
        sort-by="Cliente_Codigo"
        class="elevation-1"
        items-per-page-text= "Clientes por pgina:"
        pageText= '{0}-{1} de {2}'
      >
        <template v-slot:top>
          <v-toolbar dense flat color="white">
            <v-toolbar-title>Clientes</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Buscar"
              single-line
              dense
              hide-details
              class="shrink"
              style="margin-right:20rem"
              ></v-text-field>
              <router-link to="/createCliente">
                <v-btn @click="sendClienteVacio" dark fab x-small color="#2991c6">
                  <v-icon dark>mdi-plus</v-icon>
                </v-btn>
              </router-link>
            
          </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
           <router-link to="editCliente/">
          <v-icon
            v-b-tooltip.hover title="Editar"
            v-if=" item.Visible == 'X' "
            small
            color="#2991c6"
            class="mr-2"
            @click="sendCliente(item)"
          >
            mdi-pencil
          </v-icon>
           </router-link>
          <v-icon
            v-b-tooltip.hover title="Desactivar"
            v-if=" item.Visible == 'X' "
            small
            color="#2991c6"
            @click="desactivarCliente(item)"
          >
            mdi-eye-off
          </v-icon>
          <v-icon
            v-b-tooltip.hover title="Activar"
            v-if=" item.Visible == '' "
            small
            color="#2991c6"
            @click="activarCliente(item)"
          >
            mdi-eye
          </v-icon>
        </template>
      </v-data-table>
    </v-container>
  </v-content>
</template>

<style scoped>
.shrink{
      width: 200px;
}
.container{
  margin-left:5rem;
}
</style>

<script>
import axios from 'axios';
const ip = require('../../../../../Back-End-Planning/ip/ip')
  export default {
    data: () => ({
      search: '',
      //Diseo del datatable
      headers: [
        {
          text: 'Cdigo',
          align: 'start',
          sortable: false,
          value: 'Cliente_Codigo',
        },
        { text: 'Descripcin', value: 'Cliente_Descripcion'},
        { text: 'Regin', value: 'Region.Region_Descripcion'},
        { text: 'Industria', value: 'Industria.Industria_Descripcion' },
        { text: 'Activado', value: 'Visible', align: 'center' },
        { text: 'Acciones', value: 'actions', sortable: false, align : 'end' },
      ],
      //Elementos del datatable
      desserts: [],
      //AUTOCOMPLETE
      entries: [],
      isLoading: false,
      model: null,
      search: null,
      visible: {Visible: ''}
    }),

    created(){
      this.initialize();
      this.cleanStore();
    },

    methods: {
      initialize () {
        axios.get(ip+"/clientes")
        .then(response => {
          this.desserts = response.data
        })
      },

      cleanStore(){
      this.$store.replaceState({
        cliente: [],
        clienteTipo:0,
        proyecto: [],
        clienteFiscal: [],
        empresa:[]})
      }, 

      sendClienteVacio(){
        this.$store.state.cliente[0]= {
          Cliente_Codigo: "",
          Cliente_Descripcion: "",
          Cliente_Region: "",
          Cliente_Industria: "",
          Cliente_Empresa: "",
          Cliente_Moneda_Principal: "",
          Cliente_Observacion: "",
          Cliente_Logo: '',
          Proyectos: [],
          ClientesFiscales: [],
          Usuario_Creacion: null,
          Usuario_Modificacion: null,
        }
      },

      sendCliente(item){
        this.$store.state.cliente[0] = item
      },

      desactivarCliente (item) {
        const index = this.desserts.indexOf(item)
        var visible = {Visible: ''}
        item.Visible = ''
        confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1, item)
        &&
        axios.patch(ip+"/Clientes/"+item.Cliente_Codigo, visible)
        .then(response => {
          console.log(response)
        })
      },

      activarCliente(item) {
        const index = this.desserts.indexOf(item)
        var visible = {Visible: 'X'}
        item.Visible = 'X'
        this.desserts.splice(index, 1, item)
        axios.patch(ip+"/Clientes/"+item.Cliente_Codigo, visible)
        .then(response => {
          console.log(response)
        })
      }

    }
}
</script>