<template>
  <v-content>
    <v-container fluid>
      <v-form ref="form">
      <v-row>
        <v-col cols="12" style="max-width:82rem; margin-top:-0.5rem">
          <v-title style="font-size:1.5rem; padding-left:1rem;">Datos Generales</v-title>
          <img style="margin-top:-1rem; margin-left:1rem" width="180rem" height="40rem" v-bind:src="empresa.Empresa_Logo">
          <v-btn @click="guardar" color="#32b4dd" dark style="margin-left:40rem">Guardar</v-btn>
          <router-link to="/empresasTable">
          <v-btn @click="cleanStore" color="#ffa025" dark style="margin-left:1rem">Cancelar</v-btn>
          </router-link>
          <v-card class="pa-2" outlined tile style="margin-top:1rem;">
            <v-text-field
              outlined
              dense
              v-model="empresa.Empresa_Codigo"
              label="Cdigo"
              :rules="[rules.checkCode,rules.counterCodigo]"
              placeholder="Escribe..."
              style="display:inline-block; max-width:10rem;"
            ></v-text-field>
            <v-text-field
              outlined
              dense
              v-model="empresa.Empresa_Descripcion"
              label="Descripcin"
              :rules="[rules.checkCode,rules.counterDescripcion]"
              placeholder="Escribe..."
              style="display:inline-block; min-width:30rem; margin-left:1rem"
            ></v-text-field>
            <v-select
              dense
              outlined
              v-model="empresa.Empresa_Tipo"
              :items="tiposEmpresasDescripciones"
              label="Tipo"
              placeholder="Selecciona..."
              style="max-width:11.5rem; display:inline-block;margin-left:1rem"
            ></v-select>
            <v-select
              dense
              outlined
              v-model="empresa.Empresa_Region"
              :items="regiones_Descripciones"
              label="Regin"
              placeholder="Selecciona..."
              style="max-width:11.5rem; display:inline-block;margin-left:1rem"
            ></v-select>
            <v-select
              dense
              outlined
              v-model="empresa.Empresa_Unidad_Negocio"
              :items="unidades_NegocioDescripciones"
              label="U.Negocio"
              placeholder="Selecciona..."
              style="max-width:11.5rem; display:inline-block;margin-left:1rem"
            ></v-select>
            <v-text-field
              outlined
              background-color="white"
              dense
              v-model="empresa.Empresa_Identificador_Laboral"
              label="ID Laboral"
              :rules="[rules.checkNumbers]"
              placeholder="Escribe..."
              style="display:inline-block; max-width: 14rem;"
            ></v-text-field>
            <v-text-field
              outlined
              background-color="white"
              dense
              v-model="empresa.Empresa_Referente"
              label="Referente"
              :rules="[rules.counterReferentes]"
              placeholder="Escribe..."
              style="display:inline-block; min-width: 18rem; margin-left:1rem"
            ></v-text-field>
            <v-text-field
              outlined
              background-color="white"
              dense
              v-model="empresa.Empresa_Mail"
              label="Mail"
              :rules="[rules.checkEmail,rules.counterEmail]"
              placeholder="Escribe..."
              style="display:inline-block; min-width: 28rem; margin-left:1rem"
            ></v-text-field>
            <v-text-field
              outlined
              background-color="white"
              dense
              v-model="empresa.Empresa_Telefono"
              label="Telfono"
              :rules="[rules.checkPhoneNumber]"
              placeholder="Escribe..."
              style="display:inline-block; min-width: 16rem; margin-left:1rem"
            ></v-text-field>
            <v-textarea background-color="white" outlined placeholder="Escribe.." :counter="1000" label="Observaciones" rows="2" style="margin-bottom:-2rem">
           </v-textarea>
          </v-card>
        </v-col>
      </v-row>
      </v-form>
    </v-container>
  </v-content>
</template>

<style  scoped>
.container {
  margin-left: 1rem;
  min-width: 100rem;
}
</style>


<script>
import axios from "axios";
const ip = require('../../../../../Back-End-Planning/ip/ip')
import {checkCode,checkEmail,counterTelefono,counterCodigo, counterDescripcion,
        counterEmail,checkPhoneNumber,checkNumbers,counterReferentes} 
from '../../../validations/validations'

export default {
  data: () => ({
    codigoViejo: '',
    //Reglas de agregado y editado
    rules: {
      checkCode: checkCode,
      checkEmail: checkEmail,
      checkPhoneNumber: checkPhoneNumber,
      checkNumbers: checkNumbers,
      counterCodigo: counterCodigo,
      counterDescripcion: counterDescripcion,
      counterTelefono: counterTelefono,
      counterEmail: counterEmail,
      counterReferentes: counterReferentes,
    },
    //Elemento que se va a guardar con el UPDATE
    empresa: {
        Empresa_Codigo: '',
        Empresa_Descripcion: '',
        Empresa_Tipo: null,
        Empresa_Region: null,
        Empresa_Unidad_Negocio: null,
        Empresa_Identificador_Laboral: '',
        Empresa_Referente: '',
        Empresa_Mail: '',
        Empresa_Telefono: '',
        Empresa_Logo: 'https://iwantic.com/wp-content/uploads/2016/12/consultor-sap.png',
        Empresa_Observacion: '',
        Usuario_Creacion: 1,
        Usuario_Modificacion: 1,
    },
    unidades_Negocio: [],
    unidades_NegocioDescripciones: [],

    regiones: [],
    regiones_Descripciones: [],

    tiposEmpresas: [],
    tiposEmpresasDescripciones: [],
  }),

  created(){
      this.initialize();
  },



  methods: {
    async initialize(){
      this.loadEmpresa();
      this.loadTiposEmpresas();
      this.loadRegiones();
      this.loadUnidadesNegocio();
      await this.wait(1000); //DISPARAR EVENTO DE LOADING
      this.asignarDescripciones();
    },

      wait(time) {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve();
        }, time);
  })
    },

    loadEmpresa(){
        this.empresa = this.$store.state.empresa[0];
        this.codigoViejo = this.$store.state.empresa[0].Empresa_Codigo;
    },

    loadTiposEmpresas(){
        axios.get(ip+"/tipos_empresas/descripciones")
        .then(response => {
            this.tiposEmpresas = response.data
            this.tiposEmpresasDescripciones = response.data.map(tipo_Empresa => tipo_Empresa.Tipo_Empresa_Descripcion)
        })      
    },

    loadUnidadesNegocio(){
        axios.get(ip+"/unidades_negocios/descripciones")
        .then(response => {
            this.unidades_Negocio = response.data
            this.unidades_NegocioDescripciones = response.data.map(unidad_negocio => unidad_negocio.Unidad_Negocio_Descripcion)
        })      
    },

    loadRegiones() {
        axios.get(ip+"/regiones/descripciones")
        .then((response) => {
            this.regiones = response.data;
            this.regiones_Descripciones = response.data.map((region) => region.Region_Descripcion);
        });
    },

    guardar() {
        if(this.$refs.form.validate()){
          this.asignarKeys();
          axios.patch(ip+"/empresas/"+ this.codigoViejo, this.empresa)
          .then((response) => {
              alert(response.data)
          })
          .catch(err =>{
            alert('No se ha podido actualizar. La empresa ya existe')
            throw new Error('La empresa ya existe')
          })
        }
    },

    cleanStore(){
      this.$store.replaceState({
        cliente: [],
        clienteTipo:0,
        proyecto: [],
        clienteFiscal: [],
        empresa: []})
    },

    asignarKeys(){
        var tipoEmpresaKey = this.tiposEmpresas.filter((tipoEmpresa) => tipoEmpresa.Tipo_Empresa_Descripcion == this.empresa.Empresa_Tipo)[0].Tipo_Empresa_Key;
        var keyRegion = this.regiones.filter((region) => region.Region_Descripcion == this.empresa.Empresa_Region)[0].Region_Key;
        var unidadNegocioKey= this.unidades_Negocio.filter((unidadNegocio) => unidadNegocio.Unidad_Negocio_Descripcion == this.empresa.Empresa_Unidad_Negocio)[0].Unidad_Negocio_Key
        this.empresa.Empresa_Tipo = tipoEmpresaKey;
        this.empresa.Empresa_Region = keyRegion;
        this.empresa.Empresa_Unidad_Negocio = unidadNegocioKey;
    },

    asignarDescripciones(){
        var tipoEmpresaDesc = this.tiposEmpresas.filter((tipoEmpresa) => tipoEmpresa.Tipo_Empresa_Key == this.empresa.Empresa_Tipo)[0].Tipo_Empresa_Descripcion;
        var regionDesc = this.regiones.filter((region) => region.Region_Key == this.empresa.Empresa_Region)[0].Region_Descripcion;
        var unidadNegocioDesc = this.unidades_Negocio.filter((unidadNegocio) => unidadNegocio.Unidad_Negocio_Key == this.empresa.Empresa_Unidad_Negocio)[0].Unidad_Negocio_Descripcion
        this.empresa.Empresa_Tipo = tipoEmpresaDesc;
        this.empresa.Empresa_Region = regionDesc;
        this.empresa.Empresa_Unidad_Negocio = unidadNegocioDesc;
    }

  },
};
</script>