<template>
  <v-content>
    <v-container fluid>
      <v-form ref="form">
      <v-row>
        <v-header
          style="font-size:1.5rem; margin-left:3rem; margin-top:2rem; width:36rem"
        >{{this.$store.state.cliente[0].Cliente_Descripcion}} - {{proyecto.Proyecto_Descripcion}}</v-header>
        <v-checkbox
          style="display:inline-block;margin-top:2rem "
          v-model="proyecto.Proyecto_Facturable"
          :disabled="proyecto.Proyecto_Unidad_Negocio == 'No Aplica'"
          label="Facturable"
        ></v-checkbox>
        <v-checkbox
          v-model="proyecto.Proyecto_Newsletter"
          :color="proyecto.Proyecto_Newsletter ? 'yellow' : 'black'"
          style="display:inline-block;margin-top:2rem; margin-left:2rem"
          label="Newsletter"
        ></v-checkbox>
          <v-btn
            color="#32b4dd"
            dark
            style="margin-left:11rem;margin-top:2rem"
            @click="evaluarGuardado"
          >Guardar</v-btn>
          <v-btn @click="rollback" color="#ffa025" dark style="margin-left:1rem;margin-top:2rem">Cancelar</v-btn>
        <v-col cols="12" style="max-width:56rem;margin-left:1rem;margin-top:-1rem">
          <v-card class="pa-2" outlined tile>
            <v-text-field
              outlined
              dense
              v-model="proyecto.Proyecto_Codigo"
              label="Cdigo"
              :rules="[rules.checkCode,rules.counterCodigo]"
              placeholder="Escribe..."
              style="display:inline-block; max-width: 8rem;"
            ></v-text-field>
            <v-text-field
              outlined
              dense
              v-model="proyecto.Proyecto_Codigo_Externo"
              label="Cdigo Externo"
              :rules="[rules.checkCode,rules.counterCodigo]"
              placeholder="Escribe..."
              style="display:inline-block; margin-left:1rem; max-width: 8rem;"
            ></v-text-field>
            <v-text-field
              outlined
              dense
              v-model="proyecto.Proyecto_Descripcion"
              :rules="[rules.counterDescripcion,rules.checkCode]"
              label="Descripcin"
              placeholder="Escribe..."
              style="display:inline-block; margin-left:1rem; min-width: 24rem;"
            ></v-text-field>
            <v-select
              dense
              outlined
              v-model="proyecto.Proyecto_Importancia"
              @input="asignarSegunImportancia"
              :items="importanciasDescripciones"
              v-b-tooltip.hover="{variant: 'light'}" :title="evalTooltipTiposProyectos"
              label="Tipo"
              placeholder="Selecciona..."
              style="max-width:10rem; display:inline-block; margin-left:1rem; margin-bottom:-1.5rem"
            ></v-select>
            <v-select
              dense
              outlined
              v-model="proyecto.Proyecto_Tipo"
              :items="tiposProyectos_Descripciones"
              label="Modalidad"
              :readonly="proyecto.Proyecto_Tipo == 'No Aplica'"
              :disabled="proyecto.Proyecto_Tipo == 'No Aplica'"
              placeholder="Selecciona..."
              style="max-width:11.5rem; display:inline-block; margin-bottom:-1.5rem"
            ></v-select>
            <v-autocomplete
              v-model="proyecto.Proyecto_Unidad_Negocio"
              label="U. Negocio"
              :readonly="proyecto.Proyecto_Unidad_Negocio == 'No Aplica'"
              :disabled="proyecto.Proyecto_Unidad_Negocio == 'No Aplica'"
              :items="unidades_NegocioDescripciones"
              :loading="isLoading"
              :search-input.sync="searchUnidadesNegocio"
              outlined
              dense
              cache-items
              placeholder="Selecciona..."
              color="white"
              style="min-width:8rem; max-width:10rem; display:inline-block; margin-left:1rem; margin-bottom:-1.5rem"
            ></v-autocomplete>
            <v-autocomplete
              v-model="proyecto.Proyecto_Responsable"
              label="Responsable"
              :readonly="proyecto.Proyecto_Responsable == 'No Aplica'"
              :disabled="proyecto.Proyecto_Responsable == 'No Aplica'"
              :items="usuariosDescripciones"
              :loading="isLoading"
              :search-input.sync="searchResponsables"
              outlined
              dense
              cache-items
              placeholder="Selecciona..."
              color="white"
              style="min-width:8rem; max-width:10rem; display:inline-block; margin-left:0.5rem; margin-bottom:-1.5rem"
            ></v-autocomplete>
            <v-autocomplete
              v-model="proyecto.Proyecto_Vendedor"
              label="Vendedor"
              :readonly="proyecto.Proyecto_Vendedor == 'No Aplica'"
              :disabled="proyecto.Proyecto_Vendedor == 'No Aplica'"
              :items="usuariosDescripciones"
              :loading="isLoading"
              :search-input.sync="searchVendedores"
              outlined
              dense
              cache-items
              placeholder="Selecciona..."
              color="white"
              style="min-width:8rem; max-width:10rem; display:inline-block; margin-left:0.5rem; margin-bottom:-1.5rem"
            ></v-autocomplete>
            <v-select
              dense
              outlined
              v-model="proyecto.Proyecto_Moneda"
              :items="monedasCodigos"
              label="Moneda"
              :readonly="proyecto.Proyecto_Moneda == 'N/A'"
              :disabled="proyecto.Proyecto_Moneda == 'N/A'"
              placeholder="Selecciona..."
              style="max-width:8rem; display:inline-block; margin-left:0.5rem; margin-bottom:-1.5rem"
            ></v-select>
          </v-card>
        </v-col>

        <v-col cols="3">
          <v-textarea
            style="min-width:24rem; margin-top:-1rem"
            auto-grow
            background-color="white"
            filled
            color="black"
            label="Observaciones"
            :counter="1000"
            placeholder="Escribe.."
            rows="4"
          ></v-textarea>
        </v-col>

        <v-col cols="12" style="margin-left:1rem; max-width:56rem; margin-top:-1rem">
          <v-text style="font-size:1.5rem; padding-left:1rem;">Referentes</v-text>
          <v-card class="pa-2" outlined tile>
            <v-text-field
              outlined
              dense
              v-model="proyecto.Proyecto_Referente_Comercial"
              label="Referente Comercial"
              :rules="[rules.counterReferentes]"
              placeholder="Escribe..."
              style="display:inline-block; min-width: 18rem;"
            ></v-text-field>
            <v-text-field
              outlined
              dense
              v-model="proyecto.Proyecto_Referente_Comercial_Mail"
              label="Mail Comercial"
              :rules="[rules.counterReferentes,rules.checkEmail]"
              placeholder="Escribe..."
              style="display:inline-block; min-width: 34rem; margin-left:1rem"
            ></v-text-field>
            <v-text-field
              outlined
              dense
              v-model="proyecto.Proyecto_Referente_Tecnico"
              label="Referente Tcnico"
              :rules="[rules.counterReferentes]"
              placeholder="Escribe..."
              style="display:inline-block; min-width: 18rem; margin-bottom:-1.5rem"
            ></v-text-field>
            <v-text-field
              outlined
              dense
              v-model="proyecto.Proyecto_Referente_Tecnico_Mail"
              label="Mail Tcnico"
              :rules="[rules.counterReferentes,rules.checkEmail]"
              placeholder="Escribe..."
              style="display:inline-block; min-width: 34rem; margin-left:1rem;margin-bottom:-1.5rem"
            ></v-text-field>
          </v-card>
        </v-col>

        <v-col cols="3" style="margin-top:-1rem; min-width:26rem">
          <proyectosTecnologias :proyectoFueGuardado="proyectoFueGuardado"></proyectosTecnologias>
        </v-col>
      </v-row>
      </v-form>
    </v-container>
  </v-content>
</template>

<style scoped>
.container {
  min-width: 84rem;
  margin-top: -1rem;
}
</style>


<script>
import axios from 'axios';
const ip = require('../../../../../Back-End-Planning/ip/ip')
import proyectosTecnologias from '../Proyectos-Tecnologias/Proyectos-Tecnologias';
import {checkCode,counterCodigo,counterDescripcion,counterReferentes,checkEmail} from '../../../validations/validations'
  export default {
    components: {
      proyectosTecnologias
    },
    data: () => ({
      proyectoFueGuardado: false,
      rules: {
        checkCode: checkCode,
        counterCodigo: counterCodigo,
        counterDescripcion: counterDescripcion,
        counterReferentes: counterReferentes,
        checkEmail: checkEmail,
      },
      newsletter: '',
      searchUnidadesNegocio: '',
      searchResponsables: '',
      searchVendedores: '',
      proyecto: {},
      //Elemento que se va a guardar con el UPDATE
      defaultCode: '',
      //Keys para guardar
      unidades_Negocio: [],
      tiposProyecto: [],
      importancias: [],
      monedas: [],
      usuarios: [],
      tecnologias: [],
      //SEARCHBOX
      usuariosDescripciones: [],
      tiposProyectos_Descripciones: [],
      importanciasDescripciones: [],
      monedasCodigos: [],
      //AUTOCOMPLETE
      unidades_NegocioDescripciones: [],
      isLoading: false,
      model: null,
      search: null,
      visible: {Visible: ''}
    }),

  computed: {
  },

  watch: {
  },

  created() {
    this.initialize();
  },

  methods: {
    async initialize() {
      this.loadProyecto();
      this.loadUnidades_Negocios();
      this.loadTiposProyectos();
      this.loadImportancias();
      this.loadMonedas();
      this.loadTecnologias();
      this.loadUsuarios(); //Incluye tambien
      await this.wait(1000); //DISPARAR EVENTO DE LOADING
      if(this.proyectoFueGuardado){
        this.asignarDescripciones()
      }
    },

    wait(time) {
      return new Promise(resolve => {
        setTimeout(() => {
           resolve();
        }, time);
      })
    },

    loadProyecto(){
      this.proyecto = _.cloneDeep(this.$store.state.proyecto[0])
      this.proyecto.Proyecto_Cliente = this.$store.state.cliente[0].Cliente_Key
      this.proyecto.Proyecto_Facturable = this.evalFacturable();
      this.proyecto.Proyecto_Newsletter = this.evalNewsletter();
    },

    evalNewsletter(){
      if(this.proyecto.Proyecto_Newsletter.toString() == "Si"){
        return true
      } else return false
    },

    evalFacturable(){
      if(this.proyecto.Proyecto_Facturable == 1){
        return true
      } else return false
    },

    async evaluarGuardado() {
      if(this.proyectoFueGuardado){
        if(this.validarPorcentaje()){
        this.guardarTecnologias(); // guarda las tecnologias en la base
        this.proyecto.Tipo_Proyecto = {Tipo_Proyecto_Descripcion: this.proyecto.Proyecto_Tipo}
        this.proyecto.Responsable = {Usuario_Nombre_Completo: this.proyecto.Proyecto_Responsable}
        this.proyecto.Visible = 'X'
        this.$store.state.cliente[0].Proyectos.push(this.proyecto) // Inserta el proyecto guardado al cliente
        this.rollback(); // vuelve al cliente
        }
      } else if(this.$refs.form.validate()){
        await this.guardarProyecto();
        this.asignarDescripciones();
      }
    },

    async guardarProyecto() {
      
      var proyectoGuardado = this.proyecto.Proyecto_Codigo
      var proyectoDescripcion= this.proyecto.Proyecto_Descripcion
      if(this.$store.state.cliente[0].Proyectos.some(proyecto => proyecto.Proyecto_Codigo == proyectoGuardado ||
      proyecto.Proyecto_Descripcion == proyectoDescripcion)){
        alert('El proyecto ya existe para el cliente seleccionado')
        throw new Error('Proyecto existe')
      } else {
        this.assignKeys();
        await axios.post(ip+"/proyectos", this.proyecto)
        .then((response) => {
          console.log(this.proyecto);
        });
        await axios.get(ip+"/proyectos/"+ proyectoGuardado)
        .then((response) => {
          this.proyecto.Proyecto_Key = response.data.Proyecto_Key
        })
        this.$store.state.proyecto[0] = this.proyecto;
        this.proyectoFueGuardado = true
        }
    },

    guardarTecnologias(){
      var tecnologias = this.$store.state.proyecto[0].Tecnologias
      var seleccionado = this.importancias.filter(importancia => importancia.Proyecto_Importancia_Descripcion == this.proyecto.Proyecto_Importancia)[0]
      if(seleccionado.Proyecto_Importancia_Beneficio != "Directo"){
        return
      } else{
        for (let tecnologia of tecnologias) {
            this.asignarKey(tecnologia)
            axios.post(ip+"/Proyectos_Tecnologias", tecnologia)
              .then((response) => {
                console.log(response);
            })
        }
      }
    },

    validarPorcentaje(){
      var tecnologias = this.$store.state.proyecto[0].Tecnologias
      let contador = 0
      let flag = false
      var seleccionado = this.importancias.filter(importancia => importancia.Proyecto_Importancia_Descripcion == this.proyecto.Proyecto_Importancia)[0]
      if(seleccionado.Proyecto_Importancia_Beneficio != "Directo"){
        return true
      } else{
      for (let tecnologia of tecnologias){
        contador = contador + tecnologia.Proyecto_Tecnologia_Porcentaje
      }
      if (contador > 100){
        alert('El porcentaje de tecnologas supera el 100%')
        return flag
      } else if(contador < 100) {
        alert('El porcentaje de tecnologas no llega al 100%')
        return flag
      } else return flag = true
      }
    },

    rollback(){
      this.$store.state.proyecto[0] = {}
      if(this.$store.state.clienteTipo == 0){
        this.$router.push({ path: '/createCliente'})
      } else {
      this.$router.push({ path: '/editCliente' })
      }
    },

    loadMonedas() {
      axios.get(ip+"/monedas").then((response) => {
        this.monedas = response.data;
        this.monedasCodigos = response.data.map(
          (moneda) => moneda.Moneda_Codigo
        );
      });
    },

    loadTiposProyectos() {
      axios
        .get(ip+"/tipos_proyectos/descripciones")
        .then((response) => {
          this.tiposProyecto = response.data;
          this.tiposProyectos_Descripciones = response.data.map(
            (proyecto) => proyecto.Tipo_Proyecto_Descripcion
          );
        });
    },
    loadImportancias() {
      axios
        .get(ip+"/proyectos_importancias/descripciones")
        .then((response) => {
          this.importancias = response.data;
          this.importanciasDescripciones = response.data.map(
            (importancia) => importancia.Proyecto_Importancia_Descripcion
          );
        });
    },
      
    loadUsuarios(){
      axios.get(ip+"/usuarios/descripciones")
      .then(response => {
      this.usuarios = response.data
      this.usuariosDescripciones = response.data.map(usuario => usuario.Usuario_Nombre_Completo)
      })      
     },
    
    loadUnidades_Negocios() {
      axios
        .get(ip+"/unidades_negocios/descripciones")
        .then((response) => {
          this.unidades_Negocio = response.data;
          this.unidades_NegocioDescripciones = response.data.map(
            (unidad_negocio) => unidad_negocio.Unidad_Negocio_Descripcion
          );
        });
    },

    loadTecnologias(){
      axios.get(ip+"/tecnologias/descripciones").then((response) => {
        this.tecnologias = response.data;
      });
    },

    asignarDescripciones(){
      var desUnidad_Negocio = this.unidades_Negocio.filter(unidad_negocio => unidad_negocio.Unidad_Negocio_Key == this.proyecto.Proyecto_Unidad_Negocio)[0].Unidad_Negocio_Descripcion
      var desTipo = this.tiposProyecto.filter(tipoProyecto => tipoProyecto.Tipo_Proyecto_Key == this.proyecto.Proyecto_Tipo)[0].Tipo_Proyecto_Descripcion
      var desImportancia = this.importancias.filter(importancia => importancia.Proyecto_Importancia_Key == this.proyecto.Proyecto_Importancia)[0].Proyecto_Importancia_Descripcion
      var desMoneda = this.monedas.filter(moneda => moneda.Moneda_Key == this.proyecto.Proyecto_Moneda)[0].Moneda_Codigo
      var desResponsable = this.usuarios.filter(responsable => responsable.Usuario_Key == this.proyecto.Proyecto_Responsable)[0].Usuario_Nombre_Completo
      var desVendedor = this.usuarios.filter(responsable => responsable.Usuario_Key == this.proyecto.Proyecto_Vendedor)[0].Usuario_Nombre_Completo
      this.proyecto.Proyecto_Newsletter = this.evalNewsletter()
      this.proyecto.Proyecto_Facturable = this.evalFacturable()
      this.proyecto.Proyecto_Unidad_Negocio = desUnidad_Negocio
      this.proyecto.Proyecto_Tipo = desTipo
      this.proyecto.Proyecto_Importancia = desImportancia
      this.proyecto.Proyecto_Moneda = desMoneda
      this.proyecto.Proyecto_Responsable = desResponsable
      this.proyecto.Proyecto_Vendedor = desVendedor
    },

    asignarKey(tecnologia){
      var keyTecnologia = this.tecnologias.filter(tecno => tecno.Tecnologia_Descripcion == tecnologia.Proyecto_Tecnologia_Tecnologia)[0].Tecnologia_Key;
      tecnologia.Proyecto_Tecnologia_Tecnologia = keyTecnologia
    },

    assignKeys() {
      var keyUnidad_Negocio = this.unidades_Negocio.filter((unidad_negocio) =>
          unidad_negocio.Unidad_Negocio_Descripcion == this.proyecto.Proyecto_Unidad_Negocio)[0].Unidad_Negocio_Key;
      var keyTipo = this.tiposProyecto.filter((tipoProyecto) =>
          tipoProyecto.Tipo_Proyecto_Descripcion == this.proyecto.Proyecto_Tipo)[0].Tipo_Proyecto_Key;
      var keyImportancia = this.importancias.filter((importancia) =>
          importancia.Proyecto_Importancia_Descripcion == this.proyecto.Proyecto_Importancia)[0].Proyecto_Importancia_Key;
      var keyMoneda = this.monedas.filter((moneda) => 
          moneda.Moneda_Codigo == this.proyecto.Proyecto_Moneda)[0].Moneda_Key;
      var keyResponsable = this.usuarios.filter(usuario => usuario.Usuario_Nombre_Completo == this.proyecto.Proyecto_Responsable)[0].Usuario_Key
      var keyVendedor = this.usuarios.filter(usuario => usuario.Usuario_Nombre_Completo == this.proyecto.Proyecto_Vendedor)[0].Usuario_Key
      this.proyecto.Proyecto_Unidad_Negocio = keyUnidad_Negocio;
      this.proyecto.Proyecto_Facturable = this.asignarFacturable();
      this.proyecto.Proyecto_Newsletter = this.asignarNewsletter();
      this.proyecto.Proyecto_Tipo = keyTipo;
      this.proyecto.Proyecto_Importancia = keyImportancia;
      this.proyecto.Proyecto_Moneda = keyMoneda;
      this.proyecto.Proyecto_Responsable = keyResponsable
      this.proyecto.Proyecto_Vendedor = keyVendedor
    },

    asignarFacturable(){
      if(this.proyecto.Proyecto_Facturable == true){
        return 1
      } else return 2
    },

    asignarNewsletter(){
      if(this.proyecto.Proyecto_Newsletter == true){
        return 'Si'
      } else return 'No'
    },

    evalTooltipTiposProyectos(){
      var tooltip = this.importancias.filter(importancia => importancia.Proyecto_Importancia_Descripcion == this.proyecto.Proyecto_Importancia)[0]
      if(tooltip == undefined){
        return 'No se selecciono un Tipo'
      } else return ('Grupo: ' +tooltip.Proyecto_Importancia_Grupo+ '\n' +'Beneficio: ' +tooltip.Proyecto_Importancia_Beneficio)
    },

    asignarSegunImportancia(){
          var seleccionado = this.importancias.filter(importancia => importancia.Proyecto_Importancia_Descripcion == this.proyecto.Proyecto_Importancia)[0]
          if(seleccionado.Proyecto_Importancia_Beneficio != "Directo"){
          this.proyecto.Proyecto_Tipo = "No Aplica"
          this.proyecto.Proyecto_Unidad_Negocio = "No Aplica"
          this.proyecto.Proyecto_Responsable = "No Aplica"
          this.proyecto.Proyecto_Vendedor = "No Aplica"
          this.proyecto.Proyecto_Moneda = "N/A"
          this.proyecto.Proyecto_Facturable = false
          } else{
            this.proyecto.Proyecto_Tipo = ""
            this.proyecto.Proyecto_Unidad_Negocio = ""
            this.proyecto.Proyecto_Responsable = ""
            this.proyecto.Proyecto_Vendedor = ""
            this.proyecto.Proyecto_Moneda = ""
            this.proyecto.Proyecto_Facturable = false
            this.proyecto.Proyecto_Newsletter = false
          }
    }

  }
};
</script>